<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          fab
          dark
          color="rgba(39,55,70,1)"
          fixed
          right
          bottom
          v-bind="attrs"
          v-on="on"
          @click="newBoard()"
        >
          <v-icon size="23" color="#D5DBDB" dark>add</v-icon>
        </v-btn>
      </template>
      <v-card color="#F2F4F4">
        <v-card class="overflow-hidden">
          <v-app-bar
            absolute
            dark
            color="primary"
            elevate-on-scroll
            scroll-target="#scrolling-techniques-7"
          >
            <v-btn icon dark @click="dialog = false">
              <v-icon>arrow_back</v-icon>
            </v-btn>
            <v-toolbar-title>{{ textDialog }}</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn dark text @click="action()"> {{ textSave }} </v-btn>
            </v-toolbar-items>
          </v-app-bar>
          <v-sheet
            id="scrolling-techniques-7"
            class="overflow-y-auto"
            max-height="100vh"
            style="color: #273746"
          >
            <v-container>
              <v-card-title class="text-h5" style="color: #273746">
                Nuevo Tablero
              </v-card-title>
              <v-card-text style="color: #273746"
                >Datos del tablero</v-card-text
              >

              <v-form ref="form">
                <v-text-field
                  class="text-capitalize"
                  outlined
                  disabled
                  v-model="reference"
                  label="Referencia"
                  required
                ></v-text-field>
                <v-text-field
                  outlined
                  v-model="nomenclature"
                  label="Nomenclatura"
                  required
                ></v-text-field>
                <v-text-field
                  outlined
                  v-model="location"
                  label="Localización"
                  required
                ></v-text-field>
              </v-form>

              <item-group :config="result" v-if="dialog" />
            </v-container>
          </v-sheet>
        </v-card>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import { mapState, mapActions } from "vuex";
import ItemGroup from "@/components/item/ItemGroup.vue";
export default {
  components: { ItemGroup },
  name: "BoardNew",
  data() {
    return {
      reference: "",
      nomenclature: "",
      location: "",
      result: [],

      value: "",

      dialog: false,
      editDialog: false,
      dataEdit: null,
    };
  },
  computed: {
    ...mapState({
      data: (state) => state.customers.findCustomer,
    }),
    textDialog() {
      if (this.editDialog) return "Editar Tablero";
      return "Nuevo Tablero";
    },
    textSave() {
      if (this.editDialog) return "Guardar";
      return "Crear";
    },
    items() {
      return this.data.boardList.data;
    },
    config() {
      return this.data.boardList.config;
    },
  },

  methods: {
    ...mapActions({
      getItem: "customers/search",
      saveItem: "customers/save",
    }),
    show() {
      return this.items.length + 1;
    },

    action() {
      if (this.editDialog) {
        this.update();
      } else {
        this.create();
      }
    },
    update() {
      const newDataUpdate = this.data.boardList.data.map((a) =>
        a.reference == this.reference
          ? {
              ...a,
              nomenclature: this.nomenclature,
              location: this.location,
              result: this.result,
            }
          : a
      );
      this.data.boardList.data = newDataUpdate;
      this.saveItem(this.data);

      console.log(newDataUpdate);
      this.dialog = false;
      this.emptyData();
    },
    create() {
      //Organizando los datos y pushear el nuevo objeto.
      const dt = this.organizeData();
      this.data.boardList.data.push(dt);
      //Guardar los datos en la base de datos.
      this.saveItem(this.data);
      //Reiniciando los valores y cerrando la ventana.
      this.emptyData();
      this.dialog = false;
    },
    newBoard() {
      //Indicar que se va a crear un nuevo tablero.
      this.emptyData();
      this.editDialog = false;
      this.reference = this.items.length + 1;
      this.result = JSON.parse(JSON.stringify(this.config));
    },
    openBoard(data) {
      //Indicar que se va abrir un nuevo tablero.
      this.editDialog = true;
      this.dialog = true;
      this.toAssign(data);
      this.result = data.result;
    },
    organizeData() {
      //Organizar los datos.
      return {
        reference: this.reference,
        nomenclature: this.nomenclature,
        location: this.location,
        result: this.result,
      };
    },
    emptyData() {
      //Vaciar los datos.
      this.nomenclature = "";
      this.location = "";
      this.value = null;
    },
    toAssign(data) {
      //Asignar para poder visualizar datos guardados.
      this.reference = data.reference;
      this.nomenclature = data.nomenclature;
      this.location = data.location;
      //this.dataEdit = data.result;
    },
  },
};
</script>

<style scoped>
.code input {
  text-transform: uppercase;
}
</style>
